.multiselect__container{-webkit-box-align:center;-ms-flex-align:center;align-items:center;background-color:#fff;border-radius:2px;-webkit-box-shadow:0 1px 3px 0 #d1d1d2,0 0 0 1px #d1d1d2;box-shadow:0 1px 3px 0 #d1d1d2,0 0 0 1px #d1d1d2;-webkit-box-sizing:border-box;box-sizing:border-box;display:-webkit-box;display:-ms-flexbox;display:flex;min-height:36px;padding:4px 8px 0 8px;position:relative;width:100%}.multiselect__container:after{content:'';min-height:inherit;font-size:0;}.multiselect__container>*{color:#505050;font-size:.875rem;}.multiselect__wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;height:100%;width:100%;}.multiselect__clear-btn{cursor:pointer;align-items:center;margin-bottom:4px;margin-left:4px;}.multiselect__clear-btn svg{height:20px;width:20px;}.multiselect__options{background-color:#f6f6f6;border-radius:2px;left:0;max-height:0;overflow:hidden;position:absolute;top:calc(100% + 3px);z-index:9999;width:100%;opacity:0;transition:max-height .1s ease;}.multiselect__options.visible{max-height:200px;-webkit-box-shadow:0 1px 3px 0 #d1d1d2,0 0 0 1px #d1d1d2;box-shadow:0 1px 3px 0 #d1d1d2,0 0 0 1px #d1d1d2;opacity:1;transition:max-height .2s ease;}.multiselect__options ul{list-style:none;margin:0;padding:2px 0;max-height:200px;overflow:auto;}.multiselect__options ul li{cursor:pointer;padding:4px 8px;}.multiselect__options ul li *{pointer-events:none;}.multiselect__options ul p.multiselect__options--no-results,.multiselect__options ul p.multiselect__options--no-data{margin:0;padding:8px;text-align:center;}.multiselect__options ul li.multiselect__options--selected{background-color:#D16973;color:#fff;}.multiselect__options ul li.multiselect__options--selected:hover{background-color:#811721;}.multiselect__options ul li:hover{background-color:#dedede;}.multiselect__options ul li.arrow-selected{border:2px solid rgba(101,101,101,.5);}.multiselect__selected{background-color:#656565;border-radius:10px;color:#fff;font-size: