// Callout
.callout {
  padding: 1rem 1rem 1rem 3rem;
  background: #fffdff;
  // border: 1px solid $primary-3;
  border-left: 6px solid $primary-3;
  color: $primary-4;
  position: relative;
  max-width: 40rem;
}

.callout-info {
  border-left-color: $info;
  color: $secondary;

}

  /* SVG via a data URI! */

.callout p+p {
  margin-top: 1em;
}

.callout a {
  color: $primary-4;
  font-weight: bold;
}

 /* Tooltip container */
 .tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #555;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text */
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;

  /* Fade in tooltip */
  opacity: 0;
  transition: opacity 0.3s;
}

/* Tooltip arrow */
.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

//// Vue.js script filters

// Boutons des filtres

.filters {
  display: flex;
}

.filters>div {
  flex: 1;
}

.filters>div:last-child {
  columns: 2;
}

.filters>div:last-child div {
  column-span: all;
}

.filters label {
  display: block;
}

// cacher/montrer les recettes correspondant aux filtres avec vuejs
.fhide {
  display: none;
}

.fshow {
  display: block;
}

//// Boite des boutons de filtres,
.box {
  margin-bottom: 1.5em;
  padding: 1.5rem;
  box-shadow: rgba(0, 0, 0, 0.175) 0px 3px 5px;
  border: 1px solid $primary-2;
  border-radius: 5px
}

.box h4 {
  padding: 2px;
  margin: 1px 10px 2px 0;
  font-size: 1.2em;
  font-weight: bold;
}

.search-wrapper {
  display: block;
  justify-content: center;
  align-items: center;
  background-color: #f6f8fa;
  @media (min-width: 1024px) {
    display: flex;
  }
}

.search-module {
  position: relative;
  min-width: 60%;
}


/* Styles pour les résultats de recherche */
.search-module .search-results {
  position: absolute;
  top: 130%;
  left: 0;
  min-width: 100%;
  max-height: 600px;
  overflow-y: auto;
  background-color: #f6f8fa;
  border: 1px solid $primary-2;
  // border-top: none;
  border-radius: 0 0 4px 4px;
  z-index: 99;
    a {
        display: block;
        height: 100%;
        width: 100%;
        text-decoration: none;
        z-index: 100;
      }

    /* Ajuster en fonction de la taille de l'en-tête de la page */
}
.searchCard {
  padding: 1rem;
  margin: .8rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #FFF;

}

.searchCard:hover {
  transition: 0.1s ease-in;
  border-color: $primary-2;
  box-shadow: rgba($primary-2, 0.2) 0px 3px 3px;
}

.searchCard .badge {
  font-size: .65rem;
  @media (max-width: 768px) {
      display: none;
    }
}




